import RestClient from '../RestClient/RestClient';
import MyListConstants from './MyListConstants';
/**
 * This class is responsible of providing all the rewards API.
 */
export default class ListApi {
    static getList(accountId, params) {
        const config = {
            url: `${MyListConstants.CAM_BASE}/${accountId}/${MyListConstants.GET_LIST}`,
            credentials: 'include',
            params,
        };

        if (accountId) {
            return RestClient.get(config).then(json => json); // No need to handle catch, it is already handled in FetchHelper.js
        }
        return null;
    }

    static createList(params, accountId) {
        const config = {
            url: `${MyListConstants.CAM_BASE}/${accountId}/${MyListConstants.GET_LIST}`,
            credentials: 'include',
            params,
        };

        return RestClient.post(config).then(response => response); // No need to handle catch, it is already handled in FetchHelper.js
    }

    static deleteList(accountId, listId) {
        const config = {
            url: `${MyListConstants.CAM_BASE}/${accountId}/${MyListConstants.GET_LIST}/${listId}`,
            credentials: 'include',
        };

        return RestClient.delete(config).then(response => response); // No need to handle catch, it is already handled in FetchHelper.js
    }

    static saveToList(params, accountId, listId) {
        const config = {
            url: `${MyListConstants.CAM_BASE}/${accountId}/${MyListConstants.GET_LIST}/${listId}`,
            credentials: 'include',
            params,
        };

        return RestClient.post(config).then(response => response); // No need to handle catch, it is already handled in FetchHelper.js
    }
}



