import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import classNames from 'classnames/bind';
import List from '../List/List';
import * as styles from './VisualNav.css';

const cx = classNames.bind(styles);

const visualNavItemLinkContent = dataItem => (
    <div>
        <div className={styles.imgBlock}>
            <img src={dataItem.image} alt={dataItem.title}/>
        </div>
        <div className={styles.deptTitle}>
            <p className={styles.titleHolder}>{dataItem.title}</p>
        </div>
    </div>
);

const visualNavItemLinkRenderer = dataItem => (
    <div className={styles.visualNavComponent}>
        <Link
            className={styles.deptLink} to={dataItem.links} aria-label={dataItem.title}
            data-link-href={dataItem.links}>
            {visualNavItemLinkContent(dataItem)}
        </Link>
    </div>
);

const visualNavItemHtmlRenderer = dataItem => (
    <div className={styles.visualNavComponent}>
        <a
            className={styles.deptLink} href={dataItem.links} aria-label={dataItem.title}
            data-link-href={dataItem.links}>
            {visualNavItemLinkContent(dataItem)}
        </a>
    </div>
);

const visualNavItemRenderer = dataItem => (dataItem.isInternalUrl ?
        visualNavItemLinkRenderer(dataItem) : visualNavItemHtmlRenderer(dataItem)
);

class VisualNav extends Component {
    static defaultProps = {
        datasource: [],
        itemsPerRow: 2,
        direction: '',
        navTheme: '',
        navTitle: '',
        automationId: '',
    };

    static propTypes = {
        datasource: PropTypes.arrayOf(PropTypes.object).isRequired,
        itemsPerRow: PropTypes.number.isRequired,
        direction: PropTypes.string.isRequired,
        navTheme: PropTypes.string,
        navTitle: PropTypes.string,
        automationId: PropTypes.string,
    };

    render() {
        const { navTheme, navTitle, datasource } = this.props;
        let theme = cx('visualNavList');

        if (navTheme) {
            theme = cx(navTheme);
        }

        const title = () => {
            const id = this.props.automationId;
            const heading = (navTitle ? (<h3 data-automation-id={id} className={styles.visualTitle}>{navTitle}
            </h3>) : null);
            return heading;
        };

        return (
            <div className={theme}>
                <section className={styles.visualNavWrapper}>
                    {title()}
                </section>
                <section>
                    <div className={styles.wrapper}>
                        <List
                            listStyleClass={styles.gridTiles}
                            itemStyleClass={styles.gridTilesList}
                            datasource={datasource}
                            childRenderer={visualNavItemRenderer}
                            itemsPerRow={this.props.itemsPerRow}
                            direction={this.props.direction}
                            automationId={this.props.automationId}
                        />
                    </div>
                </section>
            </div>
        );
    }
}

export default VisualNav;



