import RestClient from '../RestClient/RestClient';
import * as RewardsApiConstants from './RewardsApiConstants';
import RestConfig from '../Common/RestConfig';
/**
 * This class is responsible of providing all the rewards API.
 */

const getHostConfig = (config) => {
    const context = RestClient.getContext();
    /* istanbul ignore else */
    if (context && context.hostname && context.hostname.indexOf('test') === -1) {
        config.host = 'account-api.jcpenney.com';
        config.isXCloudHeaderRequired = false;
    }
    return config;
};

class RewardsApi {
    static getRewards(obj) {
        if (obj && obj.accountId) {
            let config = {};
            config = RestConfig.v5AccountConfig();
            config = getHostConfig(config);
            config.url = `${RewardsApiConstants.CAM_BASE}/${obj.accountId}/${RewardsApiConstants.REWARDS_URL}`;
            config.params = {
                expand: 'points,certificates,recentactivity',
            };
            config.isAuthRequired = true;
            config.accessToken = obj.accessToken;

            return RestClient.get(config)
                .then(json => json); // catch not required already handled in ResClient/FetchHelper.js
        }
        return null;
    }

    static getHeader(hostPath) {
        const config = {
            url: 'j/global/header',
            params: {
                external: 'true',
            },
            credentials: 'include',
            host: hostPath,
            version: '/',
            // isApiTokenRequired: true,
        };
        return RestClient.get(config)
            .then(json => json);
    }

    static getFooter(hostPath) {
        const config = {
            url: 'j/global/footer',
            params: {
                external: 'true',
            },
            credentials: 'include',
            host: hostPath,
            version: '/',
            // isApiTokenRequired: true,
        };
        return RestClient.get(config)
            .then(json => json);
    }

    static getRewardsHistory(obj) {
        if (obj && obj.accountId) {
            let config = {};
            config = RestConfig.v5AccountConfig();
            config = getHostConfig(config);
            config.url = `${RewardsApiConstants.CAM_BASE}/${obj.accountId}` +
                    `/${RewardsApiConstants.REWARDS_URL}/${RewardsApiConstants.REWARDS_HISTORY}`;
            config.params = {
                page: obj.page,
                limit: obj.limit,
                filter: obj.filter,
                date: obj.date,
            };
            config.isAuthRequired = true;
            config.accessToken = obj.accessToken;

            return RestClient.get(config)
            .then(json => json); // catch not required already handled in ResClient/FetchHelper.js
        }
        return null;
    }

    static getActivityHistory(obj) {
        if (obj && obj.accountId) {
            let config = {};
            config = RestConfig.v5AccountConfig();
            config = getHostConfig(config);
            config.url = `${RewardsApiConstants.CAM_BASE}/${obj.accountId}` +
                    `/${RewardsApiConstants.REWARDS_URL}/${RewardsApiConstants.ACTIVITY_HISTORY}`;
            config.params = {
                page: obj.page,
                limit: obj.limit,
                filter: obj.filter,
                date: obj.date,
            };
            config.isAuthRequired = true;
            config.accessToken = obj.accessToken;

            return RestClient.get(config)
            .then(json => json); // catch not required already handled in ResClient/FetchHelper.js
        }
        return null;
    }

    static getPoints(params) {
        if (params && params.accountId) {
            const config = {
                url: `${RewardsApiConstants.CAM_BASE}/${params.accountId}/${RewardsApiConstants.POINTS_CARD}`,
                credentials: 'include',
            };

            return RestClient.get(config)
                .then(json => json); // catch not required already handled in ResClient/FetchHelper.js
        }
        return null;
    }

    static putRewardsProfile (params, obj) {
        let config = {};
        config = RestConfig.v5AccountConfig();
        config = getHostConfig(config);
        config.url = `${RewardsApiConstants.CAM_BASE}/${obj.accountId}/${RewardsApiConstants.REWARDS_URL}`;
        config.params = params;
        config.isAuthRequired = true;
        config.accessToken = obj.accessToken;
        config.userAction = obj.userAction;

        return RestClient.put(config)
            .then(json => json);
    }

    static updateRewardsSettings (params, obj) {
        let config = {};
        config = RestConfig.v5AccountConfig();
        config = getHostConfig(config);
        config.url = `${RewardsApiConstants.CAM_BASE}/${obj.accountId}/${RewardsApiConstants.REWARDS_URL}`;
        config.params = params;
        config.isAuthRequired = true;
        config.accessToken = obj.accessToken;

        return RestClient.patch(config)
            .then(json => json);
    }
}

export default RewardsApi;



