import React, { PureComponent, PropTypes } from 'react';
import { connect } from 'react-redux';
import { Helmet } from 'react-helmet';
import LoadSVG from 'yoda-core-components/lib/helpers/LoadSVG/LoadSVG';
import NavigationHelper from 'yoda-core-components/lib/helpers/NavigationHelper/NavigationHelper';
import classNames from 'classnames/bind';
import Button from 'yoda-core-components/lib/components/Button/Button';
import coreSprite from 'yoda-core-components/lib/assets/sprite.svg';
import siteSprite from 'yoda-site-components/lib/assets/sprite.svg';
import Layout from 'yoda-site-components/lib/components/Layout/Layout';
import ProductCartridge from 'yoda-site-components/lib/components/ProductCartridge/ProductCartridge';
import CouponList from 'yoda-site-components/lib/components/CouponList/CouponList';
import ApplyCouponInputBox from 'yoda-site-components/lib/components/ApplyCouponInputBox/ApplyCouponInputBox';
import DisplayApiErrorMessage from 'yoda-site-components/lib/components/DisplayApiErrorMessage/DisplayApiErrorMessage';
import * as AdjustmentActionTypes from 'yoda-site-components/lib/actionTypes/AdjustmentActionTypes';
import * as styles from './Coupon.css';

const cx = classNames.bind(styles);

export class CouponPage extends PureComponent {
    static defaultProps = {
        deviceType: {},
        seoTitleTags: {},
        isPreview: false,
    };

    static propTypes = {
        deviceType: PropTypes.objectOf(PropTypes.object),
        seoTitleTags: PropTypes.objectOf(PropTypes.object),
        isPreview: PropTypes.bool,
    };

    static getCouponHeader() {
        return (
            <section className={cx('couponHeader')}>
                <div className={cx('sm12', 'md6', 'lg6', 'xl6', 'noPad', 'couponTitle')}>
                    Coupons
                </div>
                <div className={cx('sm12', 'md6', 'lg6', 'xl6', 'noPad', 'boxModel')}>
                    {/* Apply Promo Code Component */}
                    <ApplyCouponInputBox autoFocus={false} />
                </div>
            </section>
        );
    }

    static getCouponList() {
        return (
            <section className={cx('couponList')}>
                <CouponList skipOfferMerge printConfig mountOnServer />
            </section>
        );
    }

    static getProductCartridge() {
        return (
            <section className={cx('rowBlock', 'bgWhite', 'productCartridge')}>
                <div className={cx('row')}>
                    <div className={cx('col12')}>
                        <ProductCartridge
                            pageType="COUPONS"
                            automationId="product-cartridge-coupon"
                            slotId="coupon1_rr"
                            loader="certona"
                            attributes={{
                                customerid: '',
                                recommendations: true,
                            }}
                            />
                    </div>
                </div>
            </section>
        );
    }

    static getAdMonetization() {
        return (
            <section className={cx('row', 'adPromo')}>
                {/* Ad Monetization ID */}
                <div id="div-gpt-ad-jcpenney_promo-3" />
            </section>
        );
    }

    constructor() {
        super();

        this.state = {
            showHideCopyText: 'seoContentHeight',
            copyTextButtonName: 'Show More',
            errorStatus: 0,
        };

        this.toggleCopyText = this.toggleCopyText.bind(this);
    }

    componentWillMount() {
        if (!__SERVER__) {
            window.jcpDLjcp = {}; // Added this object initialization to allow ensighten team to fix the issue.

            /** REDIRECTING FROM HTTP to HTTPS if not prview mode */
            if (!this.props.isPreview && (window.location.protocol === 'http:')) {
                window.location.protocol = 'https:';
            }
        }
    }

    componentWillReceiveProps(nextProps) {
        // nextProps.status is checked with 500 just to make sure if recovering apis status is also 500
        /* eslint react/prop-types: 0 */
        if (nextProps.clientCall && nextProps.status === 500) {
            // setting the state to show error on Api Failure
            this.setState({
                errorStatus: nextProps.status,
            });
        }

        const catergoryRegex = /\/m\//;
        if ((!catergoryRegex.test(window.location.href)) && (!window.location.href.includes('coupon'))) {
            NavigationHelper.navigate(window.location.href);
        }
    }

    setMetaData() {
        const { seoTitleTags } = this.props;

        const metaTags = seoTitleTags;
        let noIndexNoFollow = {};

        /** If there is no data to add to meta tags then return null, checking for property */
        if (!Object.prototype.hasOwnProperty.call(metaTags, 'seoMetaTitle')) return null;

        if (metaTags && metaTags.noIndexNoFollowRequired) {
            noIndexNoFollow = {
                name: 'robots',
                content: 'noindex, nofollow',
            };
        }

        const canonicalURL = `https://www.jcpenney.com${metaTags.canonicalURL}`;
        return (
            <div className="application">
                <Helmet>
                    {/* Just adding the og:description override */}
                    <meta property="og:description" content={metaTags.seoMetaDescription} />
                    <meta charSet="utf-8" />
                    <title>{metaTags.seoMetaTitle}</title>
                    <meta name="description" content={metaTags.seoMetaDescription} />
                    <meta name="keywords" content={metaTags.seoMetaKeyword} />
                    <link rel="canonical" href={canonicalURL} />
                    <meta {...noIndexNoFollow} />
                </Helmet>
            </div>
        );
    }

    getSignupAndRewards() {
        if (!this.props.deviceType.isDesktop) return null;
        return null;
    }

    getSeoContent() {
        const metaTags = this.props.seoTitleTags;
        if (metaTags.copyText) {
            return (
                <section className={cx('row', 'couponSeoContent')}>
                    <div className={cx('col12')}>
                        <div className={cx('seoContent')}>
                            <div className={cx(this.state.showHideCopyText)}>
                                <div dangerouslySetInnerHTML={{ __html: metaTags.copyText }} />
                            </div>
                            <Button
                                type="button"
                                automationId="seoToggleBtn"
                                className={cx('seoToggleBtn')}
                                buttonType="Text"
                                size="Sm"
                                onClick={this.toggleCopyText}
                                >
                                {this.state.copyTextButtonName}
                            </Button>
                        </div>
                    </div>
                </section>
            );
        }

        return null;
    }

    toggleCopyText() {
        if (this.state.showHideCopyText === 'seoContentHeight') {
            this.setState({ showHideCopyText: '', copyTextButtonName: 'Show Less' });
        } else {
            this.setState({
                showHideCopyText: 'seoContentHeight',
                copyTextButtonName: 'Show More',
            });
        }
    }

    renderCoupon() {
        return (
            <section className={cx('coupon-container')}>
                <LoadSVG svgPaths={[coreSprite, siteSprite]} />
                <section className={cx('row', 'couponBody')}>
                    <section className={cx('sm12', 'md12', 'lg12', 'xl12')}>
                        {/* coupon Header with Promo code component */}
                        {CouponPage.getCouponHeader()}

                        {/* coupon List component */}
                        {CouponPage.getCouponList()}

                    </section>

                    {/* Need only for the Desktop Layout */}
                    {this.getSignupAndRewards()}

                </section>

                {/* Product Cartridge enable once data is setup */}
                {CouponPage.getProductCartridge()}

                {/* Seo Content */}
                {this.getSeoContent()}

                {/* AdMonetization */}
                {CouponPage.getAdMonetization()}

            </section>
        );
    }


    render() {
        /** IF The Api actually fails both on server and client then displaying this */
        if (this.state.errorStatus === 500) {
            return (<Layout>
                <section className={cx('coupon-container')}>
                    <LoadSVG svgPaths={[coreSprite, siteSprite]} />
                    <section className={cx('row', 'couponBody')}>
                        <section className={cx('sm12', 'md12', 'lg12', 'xl12')}>
                            <h1 className={cx('errorHeading')}>OOps...</h1>
                            <p className={cx('errorText')}>Something went wrong, Make sure you are connected to internet,
                            but here are a few suggestions to keep you moving forward.</p>
                            <ul className={cx('errorSuggestions')}>
                                <li>Check the spelling of the URL and refresh the page</li>
                                <li>Return to the <a href="javascript:history.back()">previous screen</a></li>
                                <li>Start from jcpenney.com <a ui-sref="app.home" href="/">home page</a></li>
                            </ul>
                        </section>
                        {/* Product Cartridge enable once data is setup */}
                    </section>
                    {CouponPage.getProductCartridge()}
                </section>
            </Layout>
            );
        } // error page returned.

        return (
            <Layout>
                {this.setMetaData()}
                {this.renderCoupon()}
                <DisplayApiErrorMessage errorId={AdjustmentActionTypes.ADJUSTMENTS_POST_ERROR} />
                <DisplayApiErrorMessage errorId={AdjustmentActionTypes.ADJUSTMENTS_BY_ID_DELETE_ERROR} />
            </Layout>
        );
    }
}

const mapStateToProps = state => ({
    deviceType: state.context ? state.context.deviceType : {},
    seoTitleTags: (state.coupons && state.coupons.seoTitleTags) ? state.coupons.seoTitleTags : {},
    isPreview: state.context ? state.context.isPreview : false,
    status: (state.coupons && state.coupons.status),
    clientCall: (state.coupons && state.coupons.clientCall),
});

export default connect(mapStateToProps)(CouponPage);



