import RestClient from '../RestClient/RestClient';

export default class PricingApi {

    // Need to remove in future
    static getPriceByProductId(id) {
        return PricingApi.getPriceByProduct({ id });
    }
  /**
   * Function to fetch all product pricing.
   */
    static getPriceByProduct(params) {
        const pricingUrl = `products/${params.id}/pricing/root`;
        const config = {
            url: pricingUrl,
            external: false,
            credentials: 'same-origin',
            host: params.host,
        };
        return RestClient.get(config)
        .then((json) => {
            let price = {};
            if (json.data) {
                price = {
                    id: params.id,
                    amounts: json.data.amounts,
                };
            }
            return price;
        });
    }
}



