import contextFactory from 'yoda-app-shell/lib/contextFactory';
import RestClient from 'yoda-interfaces/lib/RestClient/RestClient';
import customRoutes from '../src/routes';
import rootReducer from '../src/reducers';
import rootSaga from '../src/sagas';
import rootMiddleware from '../src/middlewares';
import { staticSuffix, cachePolicy, app, micrositeName } from '../src/config';
import messagesTexts from '../src/messagesTexts';
import featureFlags from '../src/featureFlags';

const history = {};
const reducer = rootReducer;
const micrositeConfig = {
    micrositeName,
    staticSuffix,
    cachePolicy,
    app,
    messagesTexts,
    featureFlags,
};

const context = contextFactory(
    reducer,
    customRoutes,
    rootSaga,
    history,
    RestClient,
    micrositeConfig,
    rootMiddleware,
);

export default context;



