import RestClient from '../RestClient/RestClient';
import * as CatalogApiEndpoints from './CatalogApiConstants';

const visualNavmap = {
    home: CatalogApiEndpoints.VISUAL_NAVIGATION_HOME_URL,
    department: CatalogApiEndpoints.VISUAL_NAVIGATION_DEPT_URL,
};

/**
 * This class is responsible of providing all the department API.
 */
export default class DepartmentApi {

    /**
     * Function to fetch all root level departments.
    */
    static getRootDepartments(params) {
        return RestClient.get({ url: CatalogApiEndpoints.DEPARTMENTS_URL, params, encode: false })
            .then(json => json);
    }
    /*
     * Function to fetch all the departments.
     */
    static getAllDepartments(params) {
        return RestClient.get({ url: CatalogApiEndpoints.DEPARTMENTS_URL, params })
            .then(json => json);
    }

    static getDepartments(pageName, nTypeID) {
        /* istanbul ignore else */
        if (nTypeID) {
            nTypeID = `/${nTypeID}`;
        }
        const config = {
            url: `${visualNavmap[pageName]}${nTypeID}`,
            version: '/v5',
        };
        return RestClient.get(config)
            .then((json) => {
                console.log(`Parsing departments ${json}`);
                return json;
            });
    }

    static getDeptNavByDeptId(id) {
        const url = CatalogApiEndpoints.VISUAL_NAVIGATION_DEPT_OP_URL;
        const op = CatalogApiEndpoints.VISUAL_NAVIGATION_DEPT_OP;
        const config = {
            url: `${url}/${id}?${op}`,
            version: '/v5',
        };

        return RestClient.get(config)
            .then((json) => {
                console.log(`Parsing Navigavtion map ${json}`);
                return json;
            });
    }
}



