import TokenProvider from '../TokenProvider/TokenProvider';

export default class StoreUtils {
    static getUserLocation(additionalParams) {
        const geoOptions = {
            enableHighAccuracy: true,
        };
        /* istanbul ignore next */
        if (navigator.geolocation) {
            return new Promise((resolve) => {
                navigator.geolocation.getCurrentPosition((position) => {
                    let params = {
                        lat: position.coords.latitude,
                        lng: position.coords.longitude,
                        // lat: 33.0787152,
                        // lng: -96.8083063,
                    };
                    if (additionalParams) {
                        params = Object.assign({}, params, additionalParams);
                    }
                    resolve(params);
                }, (error) => {
                    resolve(error);
                }, geoOptions);
            });
        }
    // Todo: Handle error scenario properly
        return {};
    }

    static isAuthenticatedUser() {
        const userState = TokenProvider.get('DP_USER_STATE');
        return userState === '1';
    }
}



