        let config = RestConfig.v1DraftConfig();
        config = getHostConfig(config);
        config.url = url;
        config.accessToken = params.accessToken;
        config.params = params.payload;

        return RestClient.get(config)
        .then(
            /* istanbul ignore next */
            res => res)
