import EndecaSearchApi from './Endeca/EndecaSearchApi';

export default class SearchApi {
    static getSearchResults(searchTerm) {
        return EndecaSearchApi.getSearchResults(searchTerm);
    }

    static getSearchDetailResult(searchTerm) {
        return EndecaSearchApi.getSearchDetailResult(searchTerm);
    }
}



