class LoggerHelper {

    setLogger(logger) {
        this.logger = logger;
    }

    getLogger() { // eslint-disable-line class-methods-use-this
        if (!this.logger) {
            this.logger = console;
        }
        return this.logger;
    }

    createError(errorMessage, apiUrl, exception, status = 0) { // eslint-disable-line class-methods-use-this
        return {
            errorMessage,
            apiUrl,
            exception,
            status,
        };
    }
}

export default new LoggerHelper();



