    // scenario server would hint that putting the host in to the store. If it was overriden
    // then client also would use the same host so that it is consistent both server side and client side
    const hostname = serverContext.hostname ? serverContext.hostname : ''; // TODO:PR: If we ever think about using different hostname on server side. This line requires design refactoring!!
