import React, { Component, PropTypes } from 'react';
import classNames from 'classnames/bind';
import * as styles from './Coupon.css';

const cx = classNames.bind(styles);

/**
 * Columns supports rendering of
 * @prop Tag: div, section, article .....
 */
class Columns extends Component {

    static defaultProps = {
        tag: 'div',
        children: {},
        className: '',
    };

    static propTypes = {
        tag: PropTypes.string,
        children: React.PropTypes.node,
        className: React.PropTypes.string,
    };

    render() {
        const { tag, className } = this.props;
        const Tag = tag; // assigning it to a componentLike Value;
        const t = ['md5', 'md6'];
        console.log(this.props.className);
        console.log(cx(...t));
        return (
            <Tag className={cx(...className.split(','))}>
                {this.props.children}
            </Tag>
        );
    }
}

export default Columns;




