/* eslint-disable */
import React, { Component, PropTypes } from 'react';
import classNames from 'classnames/bind';
import * as styles from './Coupon.css';

const cx = classNames.bind(styles);

/**
 * Rows supports rendering of rows in a div, section etc , additional props margin padding. etc.
 */
class Rows extends Component {

    static defaultProps = {
        tag: 'div',
        children: {},
        className: '',
    };

    static propTypes = {
        tag: PropTypes.string,
        children: PropTypes.node,
        className: PropTypes.string,
    };

    render() {
        const { tag, className } = this.props;
        const Tag = tag; // assigning it to a componentLike Value;

        return (
            <Tag className={cx(styles.row, 'margin12')}>
                {this.props.children}
            </Tag>
        );
    }
}

export default Rows;



