import RestClient from '../../RestClient/RestClient';
import * as EndecaSearchApiConstants from './EndecaSearchApiConstants';

const getAutoSuggestURL = searchTerm => EndecaSearchApiConstants.ENDECASEARCH_API_URL + searchTerm;
const getSearchResultURL = searchTerm => EndecaSearchApiConstants.ENDECASEARCH_RESULT_URL + searchTerm;

export default class EndecaSearchApi {
    static getSearchResults(searchTerm) {
        return RestClient.get(getAutoSuggestURL(searchTerm))
            .then(json => (json.data.suggestions.map(suggestion => ({
                status: 200,
                term: suggestion.term,
                depts: suggestion.departments,
            })))).catch(error => ({
                status: 500,
                error,
            }));
    }

    static getSearchDetailResult(searchTerm) {
        return RestClient.get(getSearchResultURL(searchTerm))
            .then(json => ({
                status: 200,
                query: json.data.query,
                standardizedQuery: json.data.standardizedQuery,
                count: json.data.count.toString(),
                redirectUrl: json.data.redirectUrl,
            })).catch(error => ({
                status: 500,
                error,
            }));
    }
}



