import React, { Component, PropTypes } from 'react';
import { Card, Rating } from 'yoda-core-components/lib/components';
import classNames from 'classnames/bind';
import * as styles from './ProductCartridgeCard.css';

const cx = classNames.bind(styles);

class ProductCartridgeCard extends Component {
    static propTypes = {
        imageUrl: PropTypes.string,
        title: PropTypes.string,
        promotionType: PropTypes.string,
        original: PropTypes.string,
        reviewCount: PropTypes.number,
        price: PropTypes.oneOfType([
            PropTypes.number,
            PropTypes.string,
        ]),
        originalPrice: PropTypes.oneOfType([
            PropTypes.number,
            PropTypes.string,
        ]),
        rating: PropTypes.number,
        discount: PropTypes.oneOfType([
            PropTypes.number,
            PropTypes.string,
        ]),
        cardClass: PropTypes.string,
    };

    static defaultProps = {
        imageUrl: '',
        title: '',
        promotionType: '',
        discount: 0,
        original: '',
        rating: null,
        reviewCount: 0,
        price: 0,
        originalPrice: '',
        cardClass: '',
    };

    render() {
        const {
            imageUrl,
            title,
            price,
            promotionType,
            rating,
            originalPrice,
            discount,
            reviewCount,
            original,
            cardClass,
        } = this.props;

        return (
            <Card className={cx('productCartridgeCard', cardClass)}>
                <div className={styles.container}>
                    <div className={styles.imgBlock}>
                        <img className={styles.imageClass} src={imageUrl} alt=""/>
                    </div>
                    <div className={styles.detailsWrapper}>
                        <div className={styles.pricing}>
                            <div className={styles.priceWrapper}>
                                <span className={styles.price}>{price}</span>
                                <span className={styles.promotionType}>{promotionType}</span>
                            </div>
                            <div className={styles.strike}>
                                <span>{original} {originalPrice }</span>
                                <span className={styles.discount}>{discount}</span>
                            </div>
                        </div>
                        <h6 className={styles.title}>{title}</h6>
                        <div className={styles.ratingWrapper}>
                            <Rating size={10} space={50} rating={rating}/>
                            <span className={styles.reviewCount}>({reviewCount})</span>
                        </div>
                    </div>
                </div>
            </Card>
        );
    }
}

export default ProductCartridgeCard;



