/* eslint-disable */
import React, { Component } from 'react';
import classNames from 'classnames/bind';
import Layout from 'yoda-site-components/lib/components/Layout/Layout';
import * as Components from 'yoda-site-components/lib/index';
import templateGeneration from './TemplateMock';
import templateMock from './TemplateMock1.json';
import Rows from './Rows';
import Columns from './Columns';
import * as styles from './Coupon.css';

const cx = classNames.bind(styles);


class Coupons extends Component {


    renderTemplateEngine() {

        const layoutObject = templateMock.Layout;
        const pageComponentList = templateMock.pageComponentList; //Page Component List

        let layoutHtml = []; // Final Layout Html
        let i = 1; // to take care of keys
        let Widgets = [];

        Widgets['Template'] = require('./Template.jsx');
        pageComponentList.forEach((componentPath)=> {
            Widgets[componentPath] = Components[componentPath]; 
        });

        // this was causing test files to be loaded as well
       // require(`yoda-site-components/lib/components/${componentPath}/${componentPath}`).default;
       


    layoutObject.forEach(function (rowObject) {
        let colHtml = []; // columns Html data
        rowObject.rows.forEach(function (colObject) {
            let componentHtml = []; // components Html Data


            if (colObject.rows) {

                let multiLayoutHtml = [];
                let multiColHtml = [];
                let multiColObject = colObject.rows;
                let multiColProps;
                //console.log('Multi col object', colObject);
                multiColObject.forEach(function (nextObject) {
                    let componentHtmlData = [];
                    // console.log('Next Object....', nextObject);
                    nextObject.components.forEach(function (componentToRender) {
                        // component to Render in here
                        let DynamicComponent = Widgets[componentToRender.name];
                        let DynCompProps = componentToRender.props;
                        componentHtmlData.push(<DynamicComponent key={i++} {...DynCompProps} />);
                    });
                    multiColHtml.push(<Columns key={i++} {...nextObject.columns.props}>{componentHtmlData}</Columns>);
                });



                // console.log('Multi Col', colObject);
                multiLayoutHtml.push(<Columns key={i++} {...colObject.columns.props}>{multiColHtml}</Columns>);

                colHtml = multiLayoutHtml;


            }
            else {
                colObject.components.forEach(function (componentToRender) {
                    // component to Render in here
                    let DynamicComponent = Widgets[componentToRender.name];
                    let DynCompProps = componentToRender.props;
                    componentHtml.push(<DynamicComponent key={i++} {...DynCompProps} />);
                });

                colHtml.push(<Columns key={i++} {...colObject.columns.props}>{componentHtml}</Columns>);
            } // end of else  
        });

        layoutHtml.push(<Rows key={i++}>{colHtml}</Rows>);
    });

return layoutHtml;
    }


render() {
    return (
        <Layout>
            {this.renderTemplateEngine()}
        </Layout>
    );
}
}


export default Coupons;



