import React from 'react';
import { Route, Redirect } from 'react-router';
import Coupon from './Coupon';
import Coupons from './Coupons';
import Transformer from './Transformer';

// Valid URL /m/jcpenney-coupons/N-mpi6e5
// Redirect Method used for 301 redirection
const routes = (
    <Route>
        <Route path="/m/jcpenney-coupons/N-mpi6e5" component={Coupon} />
        <Redirect from="/coupon" to="m/jcpenney-coupons/N-mpi6e5" />
        <Redirect from="/qa*-coupon" to="m/jcpenney-coupons/N-mpi6e5" />
        <Redirect from="/coupons" to="m/jcpenney-coupons/N-mpi6e5" />
        <Redirect from="/m(/:pname)/N-mpi6e5(*)" to="m/jcpenney-coupons/N-mpi6e5(*)" />
        <Redirect from="/m/:pname(/)" to="m/jcpenney-coupons/N-mpi6e5" />
        <Route path="/template" component={Coupons} />
        <Route path="/transform" component={Transformer} />
        <Route path="/*" component={Coupon} />
    </Route>
);

export default routes;



