import RestClient from '../RestClient/RestClient';
import * as CatalogApiEndpoints from './CatalogApiConstants';

export default class CategoriesApi {

  /**
   * Function to fetch all root level departments.
   */
    static getCategoryById(id) {
        const categoryUrl = `${CatalogApiEndpoints.CATEGORY_BY_ID_URL}/${id}`;
        return RestClient.get(categoryUrl)
      .then((json) => {
          console.log(`Parsing departments ${json}`);
          return json;
      });
    }

    static getGalleryDetailsById(categoryId, params) {
        const config = {
            url: `${categoryId}`,
            credentials: 'include',
            params,
            version: '/v1',
            host: CatalogApiEndpoints.getGalleryhost(RestClient.getContext().hostname),
        };

        return RestClient.get(config)
            .then(json => (json));
    }

    static getZoneInfo(params) {
        const config = {
            url: `${CatalogApiEndpoints.GALLERY_ZONE_URL}`,
            credentials: 'include',
            params,
            version: '/v1',
            host: CatalogApiEndpoints.getGalleryhost(RestClient.getContext().hostname),
        };

        return RestClient.get(config)
            .then(json => (json));
    }
}



