import { find } from 'lodash';
import RestClient from '../RestClient/RestClient';
import RestConfig from '../Common/RestConfig';
import * as VendorApiConstants from './VendorApiConstants';


export default class VendorApi {

    /**
     * Function to Vendor related calls.
     */
    static getLatLong(params) {
        return RestClient.get({ url: VendorApiConstants.GET_LAT_LONG, params, external: true })
        .then((json) => {
            let result = null;
            if (json.data.results && json.data.results[0] && json.data.results[0].geometry) {
                result = json.data.results[0].geometry.location;
            } else if (json.data.status.indexOf(VendorApiConstants.ZERO_RESULTS) !== -1) {
                result = {};
            }
            return result;
        }).catch((error) => {
            console.log(`request failed ${error}`);
        });
    }

    /**
     * Function to get zipcode from a given lat long from Google Geocode API
     */
    static getZipCode(params) {
        const config = RestConfig.externalCallConfig();
        config.url = VendorApiConstants.GET_LAT_LONG;
        config.params = params;

        return RestClient.get(config)
        .then((json) => {
            let result = null;
            if (json.data.results && json.data.results[0] && json.data.results[0].address_components) {
                const postalCode = find(json.data.results[0].address_components, { types: ['postal_code'] });
                result = postalCode.long_name;
            } else if (json.data.status.indexOf(VendorApiConstants.ZERO_RESULTS) !== -1) {
                result = {};
            }
            return result;
        }).catch((error) => {
            console.log(`request failed ${error}`);
        });
    }
}



