import analytics from 'yoda-core-components/lib/helpers/Analytics/Analytics';
import * as AdjustmentActionTypes from 'yoda-site-components/lib/actionTypes/AdjustmentActionTypes';
import * as AnalyticsActionTypes from 'yoda-site-components/lib/actionTypes/AnalyticsActionTypes';
import * as CacheActionTypes from 'yoda-app-shell/lib/actionTypes/CacheActionTypes';
import * as CouponsActionTypes from 'yoda-site-components/lib//actionTypes/CouponActionTypes';
import CouponActionFilter from '../analytics/CouponActionFilter';

const couponActionFilter = new CouponActionFilter();

const CouponMiddleware = store => next => action => { // eslint-disable-line

    // Setting Coupon Page Cache in here on server side.
    if (__SERVER__) {
        switch (action.type) {
            case CouponsActionTypes.COUPONS_SET_CACHE_READY: {
                const couponData = store.getState();
                const { coupons } = couponData; // Destructuring coupons
                const ttlValue = (coupons && coupons.status === 200) ? coupons.CacheControl : 0;

                /**
                 * Dispatching Cache Action Here
                 */
                store.dispatch({
                    type: CacheActionTypes.SET_PAGE_CACHE,
                    cache: { ttl: ttlValue },
                });
                break;
            }
            default:
                break;
        }
    }

    if (!__SERVER__) {
        let digitalData = null;
        let analyticsEventName = null;
        switch (action.type) {
            case CouponsActionTypes.COUPONS_LOADED:
            case AdjustmentActionTypes.ADJUSTMENTS_GET_SUCCESS_COUPONPAGE:
                digitalData = CouponActionFilter.getCouponpageDL(store);
                digitalData = couponActionFilter.updateCommonDigitalData(digitalData);
                analyticsEventName = 'digitalDataReady';
                break;
            case AnalyticsActionTypes.UPDATE_ANALYTICS_CLICK_EVENT:
                digitalData = CouponActionFilter.getCouponDL(store);
                analyticsEventName = 'couponApplied';
                break;
            case AdjustmentActionTypes.ADJUSTMENTS_ERROR_TRIGGER:
                digitalData = CouponActionFilter.getFormErrorData(store);
                analyticsEventName = 'formError';
                break;
            default:
                break;
        }
        /* istanbul ignore else */
        if (digitalData) {
            analytics.track(analyticsEventName, digitalData);
        }
    }
    return next(action);
};

export default CouponMiddleware;



